"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger, DialogFooter } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Plus, FileText, Loader2, Pencil, Trash2, Eye } from "lucide-react"
import { documentsApi, locationsApi, rolesApi } from "@/lib/api"
import { useAuth } from "@/components/auth-provider"
import { useToast } from "@/hooks/use-toast"
import Link from "next/link"

export default function DocumentsPage() {
  const { user } = useAuth()
  const { toast } = useToast()
  const [documents, setDocuments] = useState<any[]>([])
  const [locations, setLocations] = useState<any[]>([])
  const [roles, setRoles] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [dialogOpen, setDialogOpen] = useState(false)
  const [editingDoc, setEditingDoc] = useState<any>(null)
  const [formData, setFormData] = useState({
    title: "",
    content: "",
    documentType: "",
    accessLevel: "",
    keywords: "",
  })

  const isAdmin = user?.role === "ADMIN"

  const documentTypes = [
    { value: "PERSONAL", label: "Personal Document" },
    { value: "STAGE_SPECIFIC", label: "Stage-Specific Document" },
    { value: "LOCATION_SPECIFIC", label: "Location-Specific Document" },
    { value: "GENERAL", label: "General Community Document" },
  ]

  const accessLevels = [
    { value: "PRIVATE", label: "Private - Author Only" },
    { value: "RESTRICTED", label: "Restricted - Specific Users/Roles" },
    { value: "STAGE_BASED", label: "Stage-Based - Users in specific stages" },
    { value: "LOCATION_BASED", label: "Location-Based - Users in specific locations" },
    { value: "PUBLIC", label: "Public - All community members" },
  ]

  useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    try {
      const [docs, locs, rls] = await Promise.all([
        documentsApi.getAccessible(),
        locationsApi.getAll(),
        rolesApi.getAll(),
      ])
      setDocuments(docs)
      setLocations(locs)
      setRoles(rls)
    } catch (error) {
      console.error("[v0] Error fetching data:", error)
      toast({ title: "Erro ao carregar documentos", variant: "destructive" })
    } finally {
      setLoading(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      if (editingDoc) {
        await documentsApi.update(editingDoc.id, formData)
        toast({ title: "Documento atualizado com sucesso" })
      } else {
        await documentsApi.create(formData)
        toast({ title: "Documento criado com sucesso" })
      }
      setDialogOpen(false)
      setEditingDoc(null)
      resetForm()
      fetchData()
    } catch (error) {
      toast({ title: "Erro ao salvar documento", variant: "destructive" })
    }
  }

  const handleEdit = (doc: any) => {
    setEditingDoc(doc)
    setFormData({
      title: doc.title || "",
      content: doc.content || "",
      documentType: doc.documentType || "",
      accessLevel: doc.accessLevel || "",
      keywords: doc.keywords || "",
    })
    setDialogOpen(true)
  }

  const handleDelete = async (id: number) => {
    if (!confirm("Tem certeza que deseja excluir este documento?")) return
    try {
      await documentsApi.delete(id)
      toast({ title: "Documento excluído com sucesso" })
      fetchData()
    } catch (error) {
      toast({ title: "Erro ao excluir documento", variant: "destructive" })
    }
  }

  const resetForm = () => {
    setFormData({
      title: "",
      content: "",
      documentType: "",
      accessLevel: "",
      keywords: "",
    })
  }

  const getAccessBadgeColor = (accessLevel: string) => {
    switch (accessLevel) {
      case "PUBLIC":
        return "default"
      case "PRIVATE":
        return "destructive"
      case "RESTRICTED":
        return "secondary"
      default:
        return "outline"
    }
  }

  if (loading) {
    return (
      <div className="flex h-full items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Documentos Formativos</h1>
          <p className="text-muted-foreground">Gerencie e acesse os documentos da comunidade</p>
        </div>
        {isAdmin && (
          <Dialog open={dialogOpen} onOpenChange={setDialogOpen}>
            <DialogTrigger asChild>
              <Button
                onClick={() => {
                  setEditingDoc(null)
                  resetForm()
                }}
              >
                <Plus className="mr-2 h-4 w-4" />
                Novo Documento
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
              <DialogHeader>
                <DialogTitle>{editingDoc ? "Editar Documento" : "Novo Documento"}</DialogTitle>
              </DialogHeader>
              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <Label htmlFor="title">Título</Label>
                  <Input
                    id="title"
                    value={formData.title}
                    onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                    placeholder="Título do documento"
                    required
                  />
                </div>

                <div>
                  <Label htmlFor="content">Conteúdo</Label>
                  <Textarea
                    id="content"
                    value={formData.content}
                    onChange={(e) => setFormData({ ...formData, content: e.target.value })}
                    placeholder="Conteúdo do documento formativo..."
                    rows={8}
                    required
                  />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="documentType">Tipo de Documento</Label>
                    <Select
                      value={formData.documentType}
                      onValueChange={(value) => setFormData({ ...formData, documentType: value })}
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Selecione o tipo" />
                      </SelectTrigger>
                      <SelectContent>
                        {documentTypes.map((type) => (
                          <SelectItem key={type.value} value={type.value}>
                            {type.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <Label htmlFor="accessLevel">Nível de Acesso</Label>
                    <Select
                      value={formData.accessLevel}
                      onValueChange={(value) => setFormData({ ...formData, accessLevel: value })}
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Selecione o acesso" />
                      </SelectTrigger>
                      <SelectContent>
                        {accessLevels.map((level) => (
                          <SelectItem key={level.value} value={level.value}>
                            {level.label}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div>
                  <Label htmlFor="keywords">Palavras-chave</Label>
                  <Input
                    id="keywords"
                    value={formData.keywords}
                    onChange={(e) => setFormData({ ...formData, keywords: e.target.value })}
                    placeholder="formação, espiritualidade, comunidade"
                  />
                </div>

                <DialogFooter>
                  <Button type="button" variant="outline" onClick={() => setDialogOpen(false)}>
                    Cancelar
                  </Button>
                  <Button type="submit">Salvar</Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        )}
      </div>

      {documents.length === 0 ? (
        <Card>
          <CardContent className="flex flex-col items-center justify-center py-12">
            <FileText className="mb-4 h-12 w-12 text-muted-foreground" />
            <p className="text-muted-foreground">Nenhum documento disponível</p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {documents.map((doc) => (
            <Card key={doc.id} className="transition-shadow hover:shadow-lg">
              <CardHeader>
                <div className="flex items-start justify-between">
                  <CardTitle className="line-clamp-1 flex-1">{doc.title || "Sem título"}</CardTitle>
                  {isAdmin && (
                    <div className="flex gap-1">
                      <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => handleEdit(doc)}>
                        <Pencil className="h-3 w-3" />
                      </Button>
                      <Button variant="ghost" size="icon" className="h-8 w-8" onClick={() => handleDelete(doc.id)}>
                        <Trash2 className="h-3 w-3 text-destructive" />
                      </Button>
                    </div>
                  )}
                </div>
                <CardDescription className="line-clamp-2">
                  {doc.content?.substring(0, 100) || "Sem descrição"}...
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="outline" className="text-xs">
                      {doc.documentType || "N/A"}
                    </Badge>
                    <Badge variant={getAccessBadgeColor(doc.accessLevel)} className="text-xs">
                      {doc.accessLevel || "N/A"}
                    </Badge>
                  </div>
                  {doc.keywords && (
                    <div className="text-xs text-muted-foreground">
                      <span className="font-medium">Tags:</span> {doc.keywords}
                    </div>
                  )}
                  <Button className="mt-2 w-full bg-transparent" variant="outline" asChild>
                    <Link href={`/dashboard/documents/${doc.id}`}>
                      <Eye className="mr-2 h-4 w-4" />
                      Ver Documento
                    </Link>
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
